.card {
  width: 125px;
  height: 125px;
  background-color: white;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 25px;
  box-shadow: 0px 0px 30px #c2d1fd85;
  transition: transform 330ms ease-in-out;
  cursor: pointer;
}
.card img {
  width: 100px;
  height: 100px;
}
.card:hover {
  transform: scale(1.2);
}
@media screen and (min-width: 1050px) {
  .card {
    margin: 0;
    margin-right: 50px;
    margin-bottom: 50px;
  }
}