@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500;700&display=swap);
nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 2rem 2rem 2rem;
  background-color: white;
}
nav .menu-desktop {
  display: none;
}
@media screen and (min-width: 1050px) {
  nav {
    padding: 2rem 1rem 2rem 1rem;
    background-color: transparent;
    width: 80%;
    margin: auto;
  }
  nav img {
    display: none;
  }
  nav .menu-desktop {
    display: flex;
  }
  nav .menu-desktop ul {
    display: flex;
    align-items: center;
  }
  nav .menu-desktop li {
    margin-right: 30px;
  }
  nav .menu-desktop li a:hover {
    color: #8b26f3;
  }
  nav .menu-desktop li:first-child {
    border-radius: 10px;
    background-color: #ff7267;
    color: white;
    padding: 0.5rem 1.2rem 0.5rem 1.2rem;
    box-shadow: 0px 0px 10px #f1784085;
    transition: transform 330ms ease;
    transition: background-color 1s ease;
  }
  nav .menu-desktop li:first-child:hover {
    background-color: #ffffff;
    box-shadow: 0px 0px 10px #e09c7c85;
  }
  nav .menu-desktop li:first-child:hover a {
    color: #ff7267;
  }
  nav .menu-desktop li:first-child a {
    color: white;
  }
  nav .menu-desktop li:nth-child(4) {
    margin-right: 5vw;
  }
}
@media screen and (min-width: 1050px) and (min-width: 1250px) {
  nav .menu-desktop li:nth-child(4) {
    margin-right: 15vw;
  }
}

.menu-burger {
  width: 100%;
  display: none;
}
.menu-burger ul {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.menu-burger li {
  width: 50%;
  margin: auto;
  text-align: center;
  padding: 2rem 0 2rem 0;
  background-color: #fff;
  border: 1px solid #f7fafe;
}
.menu-burger li:hover {
  background-color: #f7fafe;
  border: 1px solid #fff;
}
main {
  width: 95%;
  margin: auto;
  padding-top: 5vh;
}
@media screen and (min-width: 1050px) {
  main {
    padding-top: 10vh;
  }
}

.contain-card {
  width: 95%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin: auto;
}
@media screen and (min-width: 1050px) {
  .contain-card {
    width: 85%;
    margin: auto;
    justify-content: center;
  }
}

.business {
  display: flex;
  flex-direction: column;
  width: 80%;
  background-color: white;
  border-radius: 15px;
  margin: auto;
  margin-top: 10vh;
  padding: 2rem;
  box-shadow: 0px 0px 30px #c2d1fd85;
  margin-bottom: 5vh;
}
.business .left {
  display: flex;
  justify-content: center;
}
.business .left img {
  width: 50%;
}
.business .right {
  padding-top: 2rem;
  width: 80%;
  margin: auto;
  text-align: center;
}
.business button {
  margin-top: 1rem;
  padding: 1rem 3rem 1rem 3rem;
  background-color: #8b26f3;
  color: white;
  font-weight: bold;
  font-size: 0.8rem;
  outline: none;
  border: none;
  border-radius: 15px;
  box-shadow: 0px 0px 10px #81239485;
  cursor: pointer;
  transition: all 330ms ease-in-out;
}
.business button:hover {
  background-color: #a153f0;
  transform: scale(1.025);
}
@media screen and (min-width: 1050px) {
  .business {
    width: 80%;
    flex-direction: row;
    margin-top: 10vh;
  }
  .business .right {
    text-align: left;
  }
}
.card {
  width: 125px;
  height: 125px;
  background-color: white;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 25px;
  box-shadow: 0px 0px 30px #c2d1fd85;
  transition: transform 330ms ease-in-out;
  cursor: pointer;
}
.card img {
  width: 100px;
  height: 100px;
}
.card:hover {
  transform: scale(1.2);
}
@media screen and (min-width: 1050px) {
  .card {
    margin: 0;
    margin-right: 50px;
    margin-bottom: 50px;
  }
}
footer {
  padding-top: 2rem;
  width: 90%;
  margin: auto;
  height: 50px;
  text-align: center;
  background-color: transparent;
  font-size: 0.7rem;
  font-weight: 300;
  border-top: 1px solid lightgrey;
  padding-bottom: 2rem;
}
footer a {
  text-decoration: underline;
}
header {
  width: 90%;
  margin: auto;
  padding-bottom: 2rem;
}
header h1 {
  padding-top: 3rem;
  font-weight: 800;
  font-size: 3rem;
  text-align: center;
}
header p {
  padding-top: 1rem;
  width: 100%;
  margin: auto;
  text-align: center;
  line-height: 1.5rem;
}
header p span:nth-child(1), header p :nth-child(2) {
  color: blue;
  font-weight: bold;
}
header p span:last-child {
  color: purple;
  font-weight: bold;
}
@media screen and (min-width: 1050px) {
  header p {
    width: 60%;
  }
}
.contain-input {
  background-color: white;
  width: 77%;
  height: 40px;
  border-radius: 10px;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 0px 30px #c2d1fd85;
}
.contain-input:before {
  content: "";
  margin-left: 15px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #fff;
  border: 1px solid #3d52ff;
}
.contain-input input {
  height: 30px;
  width: 70%;
  font-size: 0.8rem;
  padding-left: 0.5rem;
  border: 0;
  outline: none;
}
.contain-input button {
  padding: 0.5rem 1rem 0.5rem 1rem;
  margin-right: 5px;
  border-radius: 5px;
  outline: none;
  border: 0;
  color: #f7fafe;
  cursor: pointer;
  font-size: 0.6rem;
  background-color: #3d52ff;
  transition: background-color 330ms ease;
}
.contain-input button:hover {
  background-color: #7280fd;
}
@media screen and (min-width: 1050px) {
  .contain-input {
    width: 47%;
    height: 60px;
  }
  .contain-input input {
    height: 50px;
    font-size: 1.2rem;
  }
  .contain-input button {
    padding: 0.8rem 2rem 0.8rem 2rem;
    font-size: 1rem;
    margin-right: 25px;
  }
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #f7fafe;
  color: #240742;
  font-family: "Montserrat", sans-serif;
  font-size: 0.9rem;
}
body:before {
  content: "";
  position: absolute;
  bottom: -400px;
  left: -500px;
  width: 800px;
  height: 800px;
  border-radius: 50%;
  background-color: #c2d1fd;
  z-index: -1;
}
body:after {
  content: "";
  position: absolute;
  top: -600px;
  right: 0;
  width: 800px;
  height: 800px;
  border-radius: 50%;
  background-color: #dfddf4;
  z-index: -1;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: #240742;
  margin: 0;
}

.category {
  color: blue;
  text-transform: uppercase;
}

.title {
  font-size: 2rem;
  color: black;
  font-weight: 800;
  padding-top: 0.5rem;
  padding-bottom: 1.5rem;
}

.para {
  font-size: 0.8rem;
  font-weight: 500;
  padding-bottom: 0.5rem;
}
